html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  background-color: pink;
  margin: 0px;
  padding: 0px;
  line-height: 1;
  color: black;
  background-color: #f8f6f6;
}
.header {
  border-bottom: 1px solid grey;
  background-color: black;
  box-shadow: 0 1px 2px grey;
  width: 100%;
  height: 32px;

  img {
    width: 20px;
  }
  nav {
    height: 100%;
    max-width: 1000px;
    display: flex;
    align-items: center;
    margin: auto;
  }
}

.content {
  display: flex;
  max-width: 1000px;
  margin: auto;
}

.sidebar {
  user-select: none;
  width: 266px;
  height: 100vh;
  padding-top: 74px;
  border-right: 1px solid #f1f1f1;
  background-color: #f8f6f6;

  div {
    margin-bottom: 10px;
    cursor: pointer;
    padding: 15px;
    &.active {
      background-color: white;
    }

    .icon {
      margin-right: 10px;
    }
  }
}

.tasks {
  background-color: white;
  height: 100vh;
  width: calc(1000px - 266px);
  padding: 74px 20px 0;

  .add-task-btn {
    cursor: pointer;
    .plus {
      color: red;
      font-size: 18px;
    }
    .add-task-text {
      color: #545454;
      font-size: 14px;
      margin-left: 15px;
    }
  }

  .add-task-dialog {
    margin-top: 10px;
    input {
      width: 100%;
      border: 1px solid #ddd;
      background-color: white;
      border-radius: 3px;
      height: 35px;
      padding-left: 10px;
      padding-right: 10px;
    }
    .add-task-actions-container {
      margin-top: 10px;
      display: flex;
      justify-content: space-between;
      .add-btn {
        background-color: red;
        color: white;
        border: 1px solid transparent;
        margin-right: 5px;
        font-weight: bold;
        font-size: 13px;
        padding: 6px 12px 7px 12px;
        border-radius: 3px !important;
        &:disabled {
          background-color: grey;
        }
        &:active {
          transform: scale(0.9);
        }
      }
      .cancel-btn {
        all: unset;
        color: #555;
        font-size: 14px;
        margin: 0px 5px;
        cursor: pointer;
      }
    }
  }
  .task-items-container {
    margin-top: 40px;
    padding: 0 20px;
    .task-item {
      display: flex;
      justify-content: space-between;
      &:not(:last-child) {
        border-bottom: 1px solid red;
      }
    }
  }
}
